<template>
  <Layout class="document-page">
    <Card class="contents">
      <template slot="title">
        <Row class="tool-bar" type="flex">
          <div class="left">
            <Input class="search-input" v-model="searchKey" placeholder="请输入关键字搜索" @keyup.native.enter="getDocumentList">
              <Button slot="append" icon="md-search" @click.native="getDocumentList"/>
            </Input>
          </div>
          <div class="right">
            <Button type="primary" @click.native="addNewDocument">新增</Button>
          </div>
        </Row>
        <Row class="tags">
          <Tag color="success" @click.native="searchTag">全部</Tag>
          <Tag color="success" v-for="(item, index) in tagList" :key="index" @click.native="searchTag(item)">{{item.tagName}}</Tag>
        </Row>
      </template>
      <Row class="document-list">
        <Tabs value="testDoc">
          <TabPane label="测试文档" name="testDoc">
            <ul class="doc-list">
              <li v-for="(item, index) in testDocList" :key="index" :class="{'selected': currentDoc.id === item.id}" @click="currentDoc = item">
                <a class="doc-title" href="javascript:void(0)" :title="item.title">
                  <Icon type="md-document"/>
                  {{item.title}}
                </a>
                <div class="doc-btns">
                  <Button class="doc-btn" size="small" type="primary" @click.native.stop="editDocument(item)">编辑</Button>
                  <Button class="doc-btn" size="small" type="error" @click.native.stop="deleteDocument(item.id)">删除</Button>
                </div>
              </li>
            </ul>
          </TabPane>
          <TabPane label="操作文档" name="operateDoc">
            <ul class="doc-list">
              <li v-for="(item, index) in operateDocList" :key="index" :class="{'selected': currentDoc.id === item.id}" @click="currentDoc = item">
                <a class="doc-title" href="javascript:void(0)" :title="item.title">
                  <Icon type="md-document"/>
                  {{item.title}}
                </a>
                <div class="doc-btns">
                  <Button class="doc-btn" size="small" type="primary" @click.native.stop="editDocument(item)">编辑</Button>
                  <Button class="doc-btn" size="small" type="error" @click.native.stop="deleteDocument(item.id)">删除</Button>
                </div>
              </li>
            </ul>
          </TabPane>
        </Tabs>
        <!-- <Row class="pagination">
          <Page :total="paginationData.total" :current="paginationData.page" show-elevator show-total show-sizer @on-change="changePage" @on-page-size-change="changePageSize"></Page>
        </Row> -->
      </Row>
    </Card>
    <Card class="document-detail">
      <mavon-editor
        class="md-editor"
        :boxShadow="false"
        :editable="false"
        :subfield="false"
        defaultOpen='preview'
        :toolbarsFlag="false"
        v-model="currentDoc.content"
        :scrollStyle="false"
        placeholder="使用文档"
        :navigation="false"
      />
    </Card>
  </Layout>
</template>

<script>
import { getDocumentList, deleteDocument } from '@/api/document'
import { getTagesByRelation } from '@/api/knowledge'

const defaultDocument = () => {
  return {
    content: `# 欢迎使用文档系统

>请选择要查看的文档或者新增一个文档`,
    id: null,
    professionId: null,
    range: 1,
    sort: 0,
    tagInfo: [],
    title: '',
    type: 2
  }
}

export default {
  name: 'document',
  data () {
    return {
      searchKey: '',
      testDocList: [],
      operateDocList: [],
      paginationData: {
        page: 1,
        pageSize: 10,
        total: 0
      },
      tagList: [],
      selectedTag: null,
      documentData: {
        content: '# 标题'
      },
      currentDoc: defaultDocument()
    }
  },
  computed: {
  },
  created () {
    this.getTestDocList()
    this.getOperateDocList()
    this.getTagList()
  },
  mounted () {},
  methods: {
    getTagList () {
      getTagesByRelation({
        type: 6
      }).then(({ data, response }) => {
        if (response.success) {
          this.tagList = data
        } else {
          this.$Message.error(response.msg)
        }
      })
    },
    getTestDocList () {
      getDocumentList({
        pageNo: this.paginationData.page,
        pageSize: this.paginationData.pageSize,
        title: this.searchKey,
        tagId: this.selectedTag ? this.selectedTag.tagId : null,
        type: 2
      }).then(resp => {
        if (resp.response.success) {
          this.testDocList = resp.data.result
          this.paginationData.total = resp.data.totalCount
        } else {
          this.$Message.error(resp.response.msg)
        }
      }).catch(err => {
        console.error(err)
      })
    },
    getOperateDocList () {
      getDocumentList({
        pageNo: this.paginationData.page,
        pageSize: this.paginationData.pageSize,
        title: this.searchKey,
        tagId: this.selectedTag ? this.selectedTag.tagId : null,
        type: 3
      }).then(resp => {
        if (resp.response.success) {
          this.operateDocList = resp.data.result
          this.paginationData.total = resp.data.totalCount
        } else {
          this.$Message.error(resp.response.msg)
        }
      }).catch(err => {
        console.error(err)
      })
    },
    addNewDocument () {
      this.$router.push({ name: 'EditDocument', params: { type: 'new', id: 0 } })
    },
    editDocument (data) {
      this.$router.push({ name: 'EditDocument', params: { type: 'detail', id: data.id } })
    },
    deleteDocument (id) {
      this.$Modal.confirm({
        title: '警告！',
        content: '删除操作不可撤销，是否确定删除？',
        onCancel: () => {},
        onOk: () => {
          deleteDocument({ ids: [id] }).then(({ data, response }) => {
            if (response.success) {
              this.$Message.success('删除成功！')
              this.getTestDocList()
              this.getOperateDocList()
              this.currentDoc = defaultDocument()
            }
          })
        }
      })
    },
    changePage (page) {
      this.paginationData.page = page
      this.getDocumentList()
    },
    changePageSize (size) {
      this.paginationData.pageSize = size
      this.getDocumentList()
    },
    searchTag (tag) {
      this.selectedTag = tag
      this.getDocumentList()
    }
  }
}
</script>

